import React, { Component } from 'react';
import styles from "../assets/css/login.module.css";
import axios from '../utils/axios';
import {NavLink} from "react-router-dom";
export default class Login extends Component {
  constructor(){
    super()
    this.state = {
      user:'',
      password:'',
    }
    
  }
  login=()=>{

    axios({
      url:'/api/login',
      method:'POST',
      data:{username:this.state.user, password:this.state.password}
    }).then(
      res => {
        // console.log(this.state.user,this.state.password)
        // console.log(res)
        // this.setState({res:res.data})
        if (res.data.mess===""){
          localStorage.setItem('user',JSON.stringify(res.data))
          this.props.history.push('/home')
        } else{
          
          alert("")
        }
      }
    )
  }
  render() {
    return (
      <div id="Login">
        <main className={styles.layout}>
            <h3> STREET</h3>
            <div className={styles.main_a}>
                <div className={styles.main_l}>
                    <p>ID/</p>
                    <ul>
                        <li><span>ID</span>
                            <input 
                            type="text" 
                            placeholder="11street ID" 
                            className={styles.txt1}
                            value={this.state.user}
                            onChange={(eve)=>{this.setState({user:eve.target.value})}}
                            />
                            
                        </li>
                        <li>
                            <span></span>
                            <input 
                            type="password" 
                            className={styles.PW}
                            value={this.state.password}
                            onChange={(eve)=>{this.setState({password:eve.target.value})}}
                            />
                            <input 
                            type="button" 
                            value="" 
                            className={styles.btn0}
                            onClick={this.login}
                            />
                        </li>
                    </ul>
                    <i></i>

                </div>
                <div className={styles.main_r}>
                    <h4></h4>
                    <h5> 11STREET</h5>
                    <p> 11STREET<br/></p>
                    <NavLink to="/Reg">
                      <b></b>
                    </NavLink>
                    
                </div>
            </div>
        </main>
      </div>
    )
  }
}

